@import "../../app.scss";


.about-section {
    width: 100%;
    height : calc(100vh - 60px);
    position: relative;
    padding-bottom : 0;
    margin-top : $header-height;


    .background{
        position : absolute;
        top : 0;
        width : 100%;
        z-index: -1;
        display : flex;

        flex-direction: row;

        .background-wave{
            height : 100%;
            width : 100%;
            overflow-x: hidden;

            img{
               height : 100%;
               width : 100%;
            }
        }

        &::after{
            content :'';
            position : absolute;
            width : 100%;
            height : 100%;
            background: rgba(255,255,255,0);
            background: linear-gradient(180deg,rgba(255,255,255,0) 0%, #ffffff 95%);
            background: -webkit-linear-gradient(180deg,rgba(255,255,255,0) 0%, #ffffff 90%);
            background: -moz-linear-gradient(180deg,rgba(255,255,255,0) 0%, #ffffff 90%);            top : 0;
            left: 0;
        }
    }

    .about-content {
        justify-content: space-between;
        display: flex;
        flex-direction: row;

        .details-section {
            display: flex;
            flex-direction: column;
            width: 70%;
            justify-content: space-between;

            h4{
                margin-bottom: 20px;
                font-weight: 400;

                span{
                    color : $primary-accent-color;
                    font-weight: 700;
                }
            }

            p {
                line-height: 2.4rem;
                text-align: justify;
            }
            
            .cv {
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-top: 20px;

                h6{
                    font-weight: 600;
                }

                a {
                    background: $action-background-color;
                    border: none;
                    outline: none;
                    margin-left: 30px;
                    border-radius: 2px;
                    color: $action-text-color;
                    padding: 0.8rem 0.6rem;
                    font-size: 1rem;
                    font-weight: 600;
                    letter-spacing: 1px;
                    text-decoration: none;
                    display : flex;
                    align-items: center;

                    svg {
                        fill: $action-text-color;
                        height: 20px;
                        width: 20px;
                        margin-right: 10px;
                    }
                }
            }
        }

        .image-section{
            display : flex;
            width : 30%;
            min-height : 300px;
            height : 380px;
            align-items: flex-end;
            justify-content: flex-end;
            
            .profile-image{
                height : 100%;
                position: relative;

                
                .img-background{
                    width : 100%;
                    height : 350px;
                    // border : 10px solid rgb(63, 81, 85);
                    border-radius: 0 40% 0 30% ; 
                    background: rgba(68, 88, 92, 0.274);
                    position : absolute;
                    bottom: 0;
                    z-index: -1;
                    
                }
                
                .image{
                    height : 100%;
                    
                    img{
                        height : 100%;
                        width : auto;
                        pointer-events: none;

                    }
                }
            }
        }
        
    }
}